<template>
    <view class="container">
        <!-- <image class="logo" src="/static/logo.png"></image>
        <view class="text-area flex flex-direction">
            <text class="title text-center text-green">基础框架更新于{{ 1591174302 | timeFrom() }}</text>
            <text class="title text-center text-green">js相关代码请查看utils/index.js</text>
        </view> -->
        <view class="flex solid-4" style="height: 100rpx;width: 400rpx;">
            <text class="bg-cyan width-max text-cut-2">leftleftleftleftleftleftleftleftleftleftleftleftleftleftleftleftleftleftleftleftleftleftleftleftleftleft</text>
            <!-- <view class="bg-brown flex-shrink">right</view> -->
        </view>
        <button type="default" @tap="tesat">测试跳转</button>
    </view>
</template>

<script>
export default {
    data() {
        return {
            title: 'Hello',
            list: [
                {
                    name: '关注'
                },
                {
                    name: '推荐'
                },
                {
                    name: '电影'
                }
            ],
            current: 0
        };
    },
    onLoad() {},
    onShow() {},
    methods: {
        change(index) {
            this.current = index;
        },
        tesat() {
            
            this.$http.get('/ebapi/store_api/hot_search',{
                pageNumber: this.pageNumber,
                pageSize: this.pageSize || 10
            }).then( res => {
                this.total = +res.data.total;
                if (this.total == 0) {
                    this.list = [];
                    setTimeout(() => {
                        if (this.refs.noData) this.refs.noData.show();
                    }, 200);
                } else if (this.total !== 0 && this.pageNumber >= 1) {
                    this.list = [...this.list, ...res.data.data.list];
                    setTimeout(() => {
                        if (this.refs.noData) this.refs.noData.hide();
                    }, 200);
                }
            });
        }
    },
    onPullDownRefresh() {}
};
</script>

<style lang="scss">
.container {
    height: 2000rpx;
}

.logo {
    height: 200rpx;
    width: 200rpx;
    margin-top: 200rpx;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50rpx;
}

.title {
    font-size: 36rpx;
}
.text {
    color: $uni-color-primary;
}
</style>
